.scales-wrapper {
  padding: var(--space-md);
}

.scales-container {
  display: flex;
  flex-direction: column;
  gap: var(--space-md);
  max-width: 1200px;
  margin: 0 auto;
}

.page-title {
  text-align: center;
  color: var(--color-text);
  margin-bottom: var(--space-lg);
  font-family: var(--font-alt);
  font-weight: var(--font-weight-semibold);
  font-size: var(--font-size-xxl);
}

.audio-alert {
  background-color: rgba(255, 100, 100, 0.1);
  border-left: 4px solid var(--color-danger);
}

.alert-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: var(--space-sm);
  padding: var(--space-sm);
  text-align: center;
}

.instrument-settings {
  padding: var(--space-md);
}

.settings-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: var(--space-md);
  margin-bottom: var(--space-lg);
}

.settings-item {
  display: flex;
  flex-direction: column;
  gap: var(--space-xs);
}

.settings-item label {
  font-weight: var(--font-weight-medium);
  color: var(--color-text);
  margin-bottom: var(--space-xxs);
}

.select-input {
  background-color: rgba(255, 255, 255, 0.05);
  border: 1px solid var(--card-border);
  border-radius: var(--radius-md);
  color: var(--color-text);
  padding: var(--space-sm);
  font-family: var(--font-primary);
  font-size: var(--font-size-md);
  transition: all 0.2s ease;
  cursor: pointer;
}

.select-input:hover, .select-input:focus {
  border-color: var(--color-primary);
  background-color: rgba(255, 255, 255, 0.1);
  outline: none;
}

.settings-sliders {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: var(--space-md);
  margin-bottom: var(--space-lg);
}

.slider-control {
  display: flex;
  flex-direction: column;
  gap: var(--space-xs);
}

.slider-control label {
  font-weight: var(--font-weight-medium);
  color: var(--color-text);
  margin-bottom: var(--space-xxs);
}

.range-input {
  width: 100%;
  accent-color: var(--color-primary);
  height: 6px;
  border-radius: var(--radius-full);
  margin: var(--space-xs) 0;
}

.button-group {
  display: flex;
  flex-wrap: wrap;
  gap: var(--space-md);
  justify-content: center;
}

.staff-container {
  padding: var(--space-md);
  margin-bottom: var(--space-md);
}

.fingering-display {
  margin-top: var(--space-md);
  display: flex;
  justify-content: center;
  min-height: 120px;
}

.selected-note-info {
  text-align: center;
}

.playback-controls {
  padding: var(--space-md);
}

.direction-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: var(--space-md);
  justify-content: center;
  margin-bottom: var(--space-md);
}

.stop-button {
  display: flex;
  justify-content: center;
  margin-bottom: var(--space-md);
}

.instructions {
  text-align: center;
  margin: var(--space-md) 0;
  color: var(--color-text-muted);
}

.instructions p {
  margin-bottom: var(--space-sm);
}

.keyboard-shortcuts {
  font-size: var(--font-size-sm);
}

.navigation-buttons {
  display: flex;
  justify-content: center;
  margin-top: var(--space-sm);
  margin-bottom: var(--space-xl);
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .settings-grid,
  .settings-sliders,
  .direction-buttons {
    grid-template-columns: 1fr;
  }
  
  .button-group {
    flex-direction: column;
    align-items: center;
  }
}

/* Trumpet valve styles */
.trumpet-interactive-fingering {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: var(--space-md) 0;
}

.trumpet-valves {
  display: flex;
  gap: 15px;
  margin-bottom: 15px;
}

.trumpet-valve {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.trumpet-valve-cap {
  width: 25px;
  height: 8px;
  background-color: #b0bec5;
  border-radius: 4px 4px 0 0;
}

.trumpet-valve-stem {
  width: 8px;
  height: 30px;
  background-color: #78909c;
}

.trumpet-valve.pressed .trumpet-valve-stem {
  height: 20px;
  background-color: #455a64;
}

.valve-number {
  position: absolute;
  top: -20px;
  font-size: var(--font-size-sm);
  color: var(--color-text-muted);
}

/* Trombone position styles */
.trombone-position-display {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: var(--space-md);
}

.trombone-slide {
  display: flex;
  align-items: center;
  height: 30px;
  margin-bottom: var(--space-md);
}

.trombone-handle {
  width: 20px;
  height: 30px;
  background-color: #b0bec5;
  border-radius: 4px;
}

.trombone-outer-slide {
  height: 15px;
  background-color: #78909c;
  border-radius: 0 4px 4px 0;
  position: relative;
  display: flex;
  align-items: center;
}

.position-marker {
  position: absolute;
  width: 5px;
  height: 20px;
  background-color: #e53935;
  border-radius: 2px;
  top: -2px;
}

.position-numbers {
  display: flex;
  gap: 30px;
  margin-top: 5px;
  font-size: var(--font-size-sm);
  color: var(--color-text-muted);
}

.position-number {
  width: 20px;
  text-align: center;
}

.position-number.current {
  color: var(--color-accent);
  font-weight: var(--font-weight-bold);
}

/* Recorder fingering styles */
.recorder-fingering {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: var(--space-md);
}

.recorder-body {
  width: 60px;
  height: 250px;
  background-color: #d7ccc8;
  border-radius: 10px 10px 20px 20px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 20px;
  box-shadow: var(--shadow-sm);
}

.recorder-mouthpiece {
  width: 30px;
  height: 10px;
  background-color: #5d4037;
  border-radius: 5px;
  position: absolute;
  top: 5px;
}

.recorder-holes {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-top: 30px;
}

.recorder-hole {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #3e2723;
}

.recorder-hole.covered {
  background-color: #d7ccc8;
  border: 2px solid #3e2723;
  position: relative;
}

.recorder-hole.covered::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 50%;
  background-color: rgba(93, 64, 55, 0.7);
}

/* Ocarina fingering styles */
.ocarina-fingering {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: var(--space-md);
}

.ocarina-body {
  width: 150px;
  height: 100px;
  background-color: #81c784;
  border-radius: 50% 50% 30% 30%;
  position: relative;
  display: flex;
  justify-content: center;
  box-shadow: var(--shadow-sm);
}

.ocarina-mouthpiece {
  width: 20px;
  height: 10px;
  background-color: #2e7d32;
  border-radius: 5px;
  position: absolute;
  top: 20px;
}

.ocarina-holes-container {
  position: absolute;
  top: 40px;
  display: flex;
  gap: 15px;
}

.ocarina-hole {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: #1b5e20;
}

.ocarina-hole.covered {
  background-color: #81c784;
  border: 2px solid #1b5e20;
  position: relative;
}

.ocarina-hole.covered::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 50%;
  background-color: rgba(46, 125, 50, 0.5);
} 